import React from 'react'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { IconLine } from '../../../atoms/IconLine'
import { i18n } from '../../../i18n'

interface IProps {
  value: string
  onFilter(value: string): void
  onConfirm(): void
}

export const StockDepositFilterInput = React.forwardRef<
  HTMLInputElement,
  IProps
>(({ value, onFilter, onConfirm }, ref) => (
  <IconLine label={i18n.t`Stock card`} labelWidth={7}>
    <FormInputStyled
      // @ts-ignore https://github.com/DefinitelyTyped/DefinitelyTyped/issues/28884
      ref={ref}
      autoFocus={true}
      placeholder={`${i18n.t`Code`.toLocaleLowerCase()} / ${i18n.t`PLU`} / ${i18n.t`Title`.toLocaleLowerCase()}`}
      value={value}
      onChange={ev => {
        onFilter(ev.target.value)
      }}
      onKeyPressCapture={ev => {
        if (ev.key === 'Enter') {
          ev.preventDefault()
          onConfirm()
        }
      }}
    />
  </IconLine>
))
