import { RM } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { Loading } from '../../../atoms/Loading'
import { useRoot } from '../../../atoms/RootProvider'
import { QStockStatsStockItems, useQStockStats } from '../../../graph/generated'
import { isPartialMatch } from '../../../helpers/filter'
import {
  IStockStatsItem,
  StockStatisticsTable,
} from '../molecules/StockStatisticsTable'

export const StockStatisticsContent = observer(() => {
  const { statistics } = useRoot()

  const { data, loading } = useQStockStats(
    {
      brandId: statistics.stockBrandId!,
    },
    { skip: !statistics.stockBrandId },
  )

  if (!statistics.stockBrandId) return null

  if (loading) return <Loading />

  const searchValue = statistics.stockSearchInput
  const partialMatch = isPartialMatch(searchValue)
  const filterItems = RM.cond([
    [RM.propEq('plu', searchValue), RM.T],
    [RM.propSatisfies(partialMatch, 'code'), RM.T],
    [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
  ])

  const sortItems = RM.sortWith<QStockStatsStockItems>([
    RM.ascend(RM.prop('code')),
  ])

  const items = sortItems(data.stockItems.filter(filterItems)).reduce(
    (accItems, stock) => {
      const existingItem = accItems.find(
        ({ code, wareUnit }) =>
          code === stock.code && wareUnit.id === stock.wareUnit.id,
      )
      const branchItem = {
        stockItemId: stock.id,
        quantity: stock.quantity,
        branchName: stock.companyBranch.name,
        itemNameAtBranch: stock.nameLabel,
      }

      if (!existingItem) {
        accItems.push({
          code: stock.code,
          wareUnit: stock.wareUnit,
          nameLabel: stock.nameLabel,
          showNamePerBranch: false,
          branches: [branchItem],
          quantity: stock.quantity,
        })
      } else {
        existingItem.branches.push(branchItem)
        if (existingItem.nameLabel !== stock.nameLabel) {
          existingItem.showNamePerBranch = true
        }
      }
      return accItems
    },
    [] as IStockStatsItem[],
  )

  return <StockStatisticsTable items={items} />
})
