import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { mediaDesktop, mediaMobile } from '../../../app/Responsive'
import { useMCreateTransfers } from '../../../graph/generated'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { useTransferModel } from '../hooks/useTransferModel'
import { MIN_TRANSFER_QUANTITY } from '../models/StockTransferItem'
import { TStockTransferModel } from '../models/StockTransferModel'

const StyledConfirmTransferButton = styled.div`
  flex: 0 0 auto;
  ${mediaDesktop`
    margin-left: 3rem;
    width: 16rem;
  `};
  @media (max-width: 760px) {
    width: unset;
    margin-left: ${Spacing.Middle};
  }
  ${mediaMobile`
    margin-left: ${Spacing.Middle};
  `};
`

export const ConfirmTransferButton = observer(() => {
  const transfer = useTransferModel()
  const { enqueueSuccess } = useSnackBar()
  const [createTransfers] = useMCreateTransfers()
  const canTransferStockItems = useCanMutate('createStockItemTransfers')

  const onConfirm = React.useCallback(async () => {
    const targetBranch = transfer.targetId
    const transfers = transfer.itemsForTransfer.map(item => ({
      stockItemId: item.id,
      quantity: item.quantity || MIN_TRANSFER_QUANTITY,
    }))

    if (transfers.length > 0 && targetBranch) {
      const transferVariables = {
        targetBranch,
        transfers,
      }

      const result = await createTransfers(transferVariables)

      if (result) {
        enqueueSuccess(i18n.t`Transfer was successfully completed!`)
        transfer.clearTransferState()
      }
    }
  }, [createTransfers, enqueueSuccess, transfer])

  if (!canTransferStockItems) {
    return null
  }

  const validTransfer = isValidTransfer(transfer)
  return (
    <StyledConfirmTransferButton>
      <TouchButton
        color={Colors.green}
        onClick={onConfirm}
        disabled={!validTransfer}
      >
        {transfer.isImportMode ? (
          <Trans>Confirm import</Trans>
        ) : (
          <Trans>Confirm transfer</Trans>
        )}
      </TouchButton>
    </StyledConfirmTransferButton>
  )
})

function isValidTransfer(transfer: TStockTransferModel) {
  if (!transfer.hasTarget) {
    return false
  }

  const transfers = transfer.itemsForTransfer
  if (transfers.length === 0) {
    return false
  }

  const hasInvalidItem = transfers.some(item => item.isQuantityValid === false)
  if (hasInvalidItem) {
    return false
  }

  return true
}
