import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { Headline } from '../../../atoms/Headline'
import { useRoot } from '../../../atoms/RootProvider'
import { FilterInput } from '../../../components/FilterInput/FilterInput'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { i18n } from '../../../i18n'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { StyledFilterWithTableContainer } from '../../menu/pages/MenuRecipeListPage'
import { useTransferModel } from '../hooks/useTransferModel'
import { ConfirmTransferButton } from '../molecules/ConfirmTransferButton'
import { ImportModeToggle } from '../molecules/ImportModeToggle'
import { StockTransferTable } from '../molecules/StockTransferTable'
import { TransferSourceBranchPicker } from '../molecules/TransferSourceBranchPicker'
import { TransferTargetBranchPicker } from '../molecules/TransferTargetBranchPicker'

const StyledTopContainer = styled.div`
  display: flex;
`

const VerticallyCenteredRow = styled(FlexRowSingle)`
  &,
  & > * {
    display: flex;
    align-items: center;
  }
`

export const StockTransferPage: React.FC = props => {
  const { user } = useRoot()
  const canTransferItems = useCanMutate('createStockItemTransfers')
  const canAccessPage = canTransferItems && !user.hasSingleBranch

  const { filter } = useTransferModel()

  return (
    <FullPageLayout
      pageTitle={i18n.t`Stock transfer`}
      useBackButton={true}
      topContent={
        <StyledTopContainer>
          <ConfirmTransferButton />
        </StyledTopContainer>
      }
    >
      <PageLayoutFullHeightItem forcePercentHeightInMobile={100}>
        <Card stretchToParent={true}>
          {canAccessPage ? (
            <StyledFilterWithTableContainer>
              <div className="filter">
                <VerticallyCenteredRow justify="start" flexWrap="wrap">
                  <FlexColumn>
                    <TransferSourceBranchPicker />
                  </FlexColumn>
                  <FlexColumn>
                    <TransferTargetBranchPicker />
                  </FlexColumn>
                  <FlexColumn>
                    <FilterInput
                      model={filter}
                      placeholder={`${i18n.t`Code`.toLocaleLowerCase()} / ${i18n.t`PLU`} / ${i18n.t`Title`.toLocaleLowerCase()}`}
                      debounceIntervalMs={200}
                    />
                  </FlexColumn>
                  <FlexColumn>
                    <ImportModeToggle />
                  </FlexColumn>
                </VerticallyCenteredRow>
              </div>
              <div className="table">
                <StockTransferTable />
              </div>
            </StyledFilterWithTableContainer>
          ) : (
            <Headline>
              <Trans>Can't access page</Trans>
            </Headline>
          )}
        </Card>
      </PageLayoutFullHeightItem>
    </FullPageLayout>
  )
}
