import { autorun } from 'mobx'
import React from 'react'

import { useRoot } from '../atoms/RootProvider'
import { FCompanyBranchFragment } from '../graph/generated'

export function useAutorunWhenSingleBranch(
  onSingleBranch: (branch: FCompanyBranchFragment) => void,
) {
  const { user } = useRoot()
  React.useEffect(() =>
    autorun(() => {
      if (user.isReady && user.hasSingleBranch) {
        onSingleBranch(user.branches[0])
      }
    }),
  )
}
