import { observer } from 'mobx-react-lite'
import React from 'react'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { i18n } from '../../../i18n'
import { numberRx } from '../../../locale/numeral'
import { useTransferModel } from '../hooks/useTransferModel'

interface IProps {
  stockItemId: ID
}

export const StockTransferAmountInput = observer<IProps>(({ stockItemId }) => {
  const transfer = useTransferModel()

  const inputRef = React.useRef<HTMLInputElement | null>(null)

  const onChange = React.useCallback(
    (ev: React.ChangeEvent<HTMLInputElement>) => {
      const { value } = ev.target
      transfer.setQuantity(stockItemId, value)
    },
    [stockItemId, transfer],
  )

  const onFocus = React.useCallback(() => {
    if (inputRef.current) {
      inputRef.current.select()
    }
  }, [])

  return (
    <FormInputStyled
      type="text"
      inputFullWidth={true}
      value={transfer.getItemRawQuantity(stockItemId)}
      disabled={!transfer.hasTarget}
      onChange={onChange}
      onFocus={onFocus}
      placeholder={i18n.t`to transfer`}
      pattern={numberRx.source}
      invalid={!transfer.isItemQuantityValid(stockItemId)}
    />
  )
})
