import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { ROUTE_MENU_STOCK } from '../../menu/menu.routes'
import { StockDashboardPage } from '../../dashboard/pages/StockDashboardPage'
import { StockDepositPage } from '../pages/StockDepositPage'
import { StockStatisticsPage } from '../pages/StockStatisticsPage'
import { StockTransferPage } from '../pages/StockTransferPage'
import {
  ROUTE_STOCK,
  ROUTE_STOCK_DEPOSIT,
  ROUTE_STOCK_OVERVIEW,
  ROUTE_STOCK_STATS,
  ROUTE_STOCK_TRANSFER,
} from '../stock.routes'

export const StockRouting: React.FC = () => (
  <Switch>
    <SecureRoute path={ROUTE_STOCK} exact component={StockDashboardPage} />
    <SecureRoute path={ROUTE_STOCK_STATS} component={StockStatisticsPage} />
    <SecureRoute path={ROUTE_STOCK_TRANSFER} component={StockTransferPage} />
    <SecureRoute path={ROUTE_STOCK_DEPOSIT} component={StockDepositPage} />
    <Redirect to={ROUTE_MENU_STOCK} from={ROUTE_STOCK_OVERVIEW} />
    <Route component={NotFoundPage} />
  </Switch>
)

export default StockRouting
