import { ArrowRightAlt } from '@material-ui/icons'
import { idPropEq, RM } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'
import { useTransferModel } from '../hooks/useTransferModel'

export const TransferTargetBranchPicker = observer(() => {
  const { user } = useRoot()
  const transfer = useTransferModel()

  const branchesWithoutSourceBranch = RM.reject(
    idPropEq(transfer.sourceId),
    user.branches,
  )

  return (
    <IconLine icon={ArrowRightAlt}>
      <CompanyBranchPicker
        selectedBranches={transfer.targetId ? [transfer.targetId] : []}
        onSelectionChange={([branch]) => transfer.setTargetId(branch)}
        onSelectionClear={() => transfer.setTargetId(null)}
        isMulti={false}
        isClearable={false}
        getDropdownProps={() => ({ width: 20 })}
        hideWhenSingleBranch={true}
        branches={branchesWithoutSourceBranch}
      />
    </IconLine>
  )
})
