import { Trans } from '@lingui/macro'
import React from 'react'

import { FormColumn, FormContainer } from '../../../atoms/FormContainer'
import { Headline } from '../../../atoms/Headline'
import { isPartialMatch } from '../../../helpers/filter'
import { RM } from '../../../helpers/ramda'
import { StockDepositConfirmButton } from '../molecules/StockDepositConfirmButton'
import { StockDepositFilterInput } from '../molecules/StockDepositFilterInput'
import { StockDepositQuantityInput } from '../molecules/StockDepositQuantityInput'
import { StockDepositResultTable } from '../molecules/StockDepositResultTable'
import { TStockItem } from './StockDepositLoader'

interface IProps {
  stockItems: RoA<TStockItem>
  onDeposit(stockItem: TStockItem, quantity: number): void
}

interface IState {
  targetStockItem: Maybe<TStockItem>
  filteredStockItems: TStockItem[]
  searchValue: string
  inputQuantity: number
}

export class StockDepositControl extends React.Component<IProps, IState> {
  state: IState = {
    targetStockItem: null,
    filteredStockItems: [],
    searchValue: '',
    inputQuantity: 0,
  }
  componentDidUpdate(prevProps: IProps) {
    if (prevProps.stockItems !== this.props.stockItems) {
      this.onFilter(this.state.searchValue)
    }
  }
  filterInputRef = React.createRef<HTMLInputElement>()
  quantityInputRef = React.createRef<HTMLInputElement>()
  onFilter = (searchValue: string) => {
    if (searchValue.trim() === '') {
      this.setState({
        filteredStockItems: [],
        searchValue: '',
        targetStockItem: null,
      })
      return
    }
    const partialMatch = isPartialMatch(searchValue)
    const filteredStockItems = this.props.stockItems.filter(
      RM.cond([
        [RM.propEq('plu', searchValue), RM.T],
        [RM.propSatisfies(partialMatch, 'code'), RM.T],
        [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
      ]),
    )
    let targetStockItem: Maybe<TStockItem> = null
    if (filteredStockItems.length === 1) {
      targetStockItem = RM.head(filteredStockItems)!
    }
    this.setState({
      filteredStockItems,
      searchValue,
      targetStockItem,
    })
  }
  onConfirmFilter = () => {
    if (this.state.filteredStockItems.length === 1) {
      this.quantityInputRef.current!.focus()
    }
  }
  onQuantity = (quantity: number) => {
    this.setState({ inputQuantity: quantity })
  }
  onSelectStockItem = (stockItem: TStockItem) => {
    this.setState(
      {
        targetStockItem: stockItem,
        filteredStockItems: [stockItem],
      },
      () => {
        this.quantityInputRef.current!.focus()
      },
    )
  }
  onConfirm = () => {
    const { targetStockItem, inputQuantity } = this.state
    if (targetStockItem && inputQuantity !== 0) {
      this.props.onDeposit(targetStockItem, inputQuantity)
    }
    this.setState(
      { targetStockItem: null, searchValue: '', inputQuantity: 0 },
      () => {
        this.quantityInputRef.current!.value = ''
        this.filterInputRef.current!.focus()
      },
    )
  }
  render() {
    const {
      searchValue,
      filteredStockItems,
      targetStockItem,
      inputQuantity,
    } = this.state
    const hasSearchValue = searchValue.trim() !== ''
    const isNoneFind = filteredStockItems.length === 0
    const isMultiFind = filteredStockItems.length > 1
    return (
      <>
        <FormContainer>
          <FormColumn>
            <StockDepositFilterInput
              onFilter={this.onFilter}
              onConfirm={this.onConfirmFilter}
              value={searchValue}
              ref={this.filterInputRef}
            />
          </FormColumn>
          <FormColumn>
            <StockDepositQuantityInput
              enabled={targetStockItem !== null}
              onChange={this.onQuantity}
              onConfirm={this.onConfirm}
              ref={this.quantityInputRef}
            />
          </FormColumn>
          <FormColumn>
            <StockDepositConfirmButton
              cardName={targetStockItem ? targetStockItem.nameLabel : ''}
              quantity={inputQuantity}
              wareUnit={targetStockItem ? targetStockItem.wareUnit : null}
              onConfirm={this.onConfirm}
            />
          </FormColumn>
        </FormContainer>
        <Headline>
          {hasSearchValue && isMultiFind && (
            <Trans>
              Continue typing to find single stock card or select one from the
              list
            </Trans>
          )}
          {hasSearchValue && isNoneFind && (
            <Trans>No stock card found from entered search value</Trans>
          )}
          {hasSearchValue && targetStockItem && (
            <Trans>
              Stock card <b>{targetStockItem.nameLabel}</b> is selected
            </Trans>
          )}
        </Headline>
        {hasSearchValue && (
          <StockDepositResultTable
            searchValue={searchValue}
            stockItems={filteredStockItems}
            onSelect={this.onSelectStockItem}
          />
        )}
      </>
    )
  }
}
