import { i18n } from '@lingui/core'
import { Search, Store } from '@material-ui/icons'
import { idPropEq } from '@speedlo/tools'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { IconLine } from '../../../atoms/IconLine'
import { MarginBox } from '../../../atoms/MarginBox'
import { useRoot } from '../../../atoms/RootProvider'
import { Sizer } from '../../../atoms/Sizer'
import { CompanyBrandDropdown } from '../../../molecules/CompanyBrandDropdown'

const StyledFlexRowSingle = styled(FlexRowSingle)`
  flex-wrap: wrap;
`

export const StockStatisticsFilter = () => {
  const { user, statistics } = useRoot()

  return useObserver(() => (
    <StyledFlexRowSingle justify="flex-start">
      <FlexColumn>
        <MarginBox margin="0 1rem 0 0">
          <IconLine icon={Store}>
            <Sizer width={16}>
              <CompanyBrandDropdown
                selectedBrand={
                  user.brands.find(idPropEq(statistics.stockBrandId)) || null
                }
                onSelection={brand => {
                  statistics.setStockBrand(brand ? brand.id : null)
                }}
              />
            </Sizer>
          </IconLine>
        </MarginBox>
      </FlexColumn>
      <FlexColumn>
        <IconLine icon={Search}>
          <Sizer width={14}>
            <FormInputStyled
              defaultValue={statistics.stockSearchInput}
              inputFullWidth={true}
              onChange={ev => statistics.setStockFilter(ev.target.value)}
              placeholder={i18n.t`Filter by name or code`}
            />
          </Sizer>
        </IconLine>
      </FlexColumn>
    </StyledFlexRowSingle>
  ))
}
