import React from 'react'
import { Index } from 'react-virtualized'
import styled from 'styled-components/macro'

import { DataList } from '../../../atoms/DataList'
import { QStockStatsWareUnit } from '../../../graph/generated'
import { Colors } from '../../../styling'

interface IProps {
  items: RoA<IStockStatsItem>
}

export interface IStockStatsItem {
  code: string
  wareUnit: QStockStatsWareUnit
  quantity: number
  nameLabel: string
  branches: Array<IStockStatsBranchItem>
  showNamePerBranch: boolean
}

interface IStockStatsBranchItem {
  stockItemId: ID
  branchName: string
  quantity: number
  itemNameAtBranch: string
}

const itemLineHeight = 20
const branchItemLineHeight = 20
const lineSpacing = 15

const StyledStockStatisticsTable = styled.div`
  margin: 1rem;

  & .stock-item {
    display: grid;
    grid-gap: 0 4px;
    grid-template-columns: 5rem 1fr;
    grid-template-rows: ${itemLineHeight}px auto;
  }
  & .code {
    grid-column: 1;
    grid-row: 1;
  }
  & .header {
    grid-column: 2;
    grid-row: 1;
    white-space: nowrap;
    border-bottom: 1px solid ${Colors.greyMiddle};
    & .quantity {
      font-weight: bold;
    }
  }
  & .branches {
    grid-column: 2;
    grid-row: 2;
    margin-top: 0.2rem;
  }
  & .header,
  & .branchItem {
    display: flex;
    flex-direction: row;
  }
  & .header .name,
  & .branches .branchName {
    flex: 1 1 auto;
    max-width: 20rem;
  }
  & .header .quantity,
  & .branches .quantity {
    flex: 0 0 5rem;
    margin-right: 1rem;
    text-align: right;
  }
  & .branchItem,
  & .header .quantity {
    font-size: 0.9rem;
  }
  & .branchItem {
    height: ${branchItemLineHeight}px;
    overflow: hidden;
  }
  & .branchName {
    color: ${Colors.greyMiddle};
  }
  & .nameAtBranch {
    flex: 2 1 auto;
    color: ${Colors.greyMiddle};
  }
`

export const StockStatisticsTable: React.FC<IProps> = ({ items }) => {
  function getRowHeight({ index }: Index) {
    return (
      itemLineHeight +
      lineSpacing +
      items[index].branches!.length * branchItemLineHeight
    )
  }

  return (
    <DataList
      items={items}
      rowHeight={getRowHeight}
      heightStretch={true}
      renderItem={item => (
        <StyledStockStatisticsTable>
          <div className="stock-item">
            <div className="code">{item.code}</div>
            <div className="header">
              <div className="name">{item.nameLabel}</div>
              <div className="quantity">
                {item.quantity} {item.wareUnit.code}
              </div>
              <div className="nameAtBranch">&nbsp;</div>
            </div>
            <div className="branches">
              {item.branches.map(branchItem => (
                <div className="branchItem" key={branchItem.branchName}>
                  <div className="branchName">{branchItem.branchName}</div>
                  <div className="quantity">
                    {branchItem.quantity} {item.wareUnit.code}
                  </div>
                  {item.showNamePerBranch && (
                    <div className="nameAtBranch">
                      {branchItem.itemNameAtBranch}
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        </StyledStockStatisticsTable>
      )}
    />
  )
}
