import { RM } from '@speedlo/tools'
import React from 'react'

import { useRoot } from '../atoms/RootProvider'
import { ISelectControlProps, SelectControl } from '../atoms/SelectControl'
import { FCompanyBranchBrand } from '../graph/generated'
import { i18n } from '../i18n'

type TBrand = FCompanyBranchBrand

interface IProps extends ISelectControlProps<TBrand> {
  brands?: RoA<TBrand>
  selectedBrand: Maybe<TBrand>
  onSelection(brand: Maybe<TBrand>): void
}

export const CompanyBrandDropdown: React.FC<IProps> = ({
  brands,
  selectedBrand,
  onSelection,
  ...props
}) => {
  const { user } = useRoot()

  return (
    <SelectControl<TBrand>
      placeholder={i18n.t`Select brand`}
      options={brands || user.brands}
      value={selectedBrand}
      formatOptionLabel={RM.prop('name')}
      getOptionValue={RM.prop('id')}
      getOptionLabel={RM.prop('name')}
      isClearable={true}
      isSearchable={true}
      onChange={value => onSelection(value as Maybe<TBrand>)}
      autoFocus={true}
      {...props}
    />
  )
}
