import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { Sizer } from '../../../atoms/Sizer'
import { ImportCheckbox } from '../atoms/ImportCheckbox'
import { useTransferModel } from '../hooks/useTransferModel'

export const ImportModeToggle = observer(() => {
  const transfer = useTransferModel()

  return (
    <>
      <ImportCheckbox
        checked={transfer.isImportMode}
        onChange={transfer.toggleImportMode}
      />
      <Sizer width={10}>
        <Trans>Enable import mode</Trans>
      </Sizer>
    </>
  )
})
