import { observer } from 'mobx-react-lite'

import { useRoot } from '../../../atoms/RootProvider'
import {
  QMenuStockDepositStockItems,
  useMMenuStockQuantity,
  useQMenuStockDeposit,
} from '../../../graph/generated'

export type TStockItem = QMenuStockDepositStockItems

export type TDepositFn = (stockItem: TStockItem, quantity: number) => void

interface IProps {
  children(stockItems: RoA<TStockItem>, onDeposit: TDepositFn): ReactNode
}

export const StockDepositLoader = observer<IProps>(({ children }) => {
  const { settings } = useRoot()
  const { data, loading } = useQMenuStockDeposit({
    branchId: settings.branchId,
  })
  const [changeQuantity] = useMMenuStockQuantity()
  const onDeposit: TDepositFn = (stockItem, quantity) => {
    changeQuantity({
      stockItem: stockItem.id,
      quantityChange: quantity,
    })
  }
  return children(loading ? [] : data.stockItems, onDeposit)
})
