import numeral from 'numeral'
import React from 'react'

import { FormInputStyled } from '../../../atoms/FormInputStyled'

interface IProps {
  enabled: boolean
  onChange(quantity: number): void
  onConfirm(): void
}

export const StockDepositQuantityInput = React.forwardRef<
  HTMLInputElement,
  IProps
>(({ enabled, onChange, onConfirm }, ref) => (
  <FormInputStyled
    // @ts-ignore https://github.com/DefinitelyTyped/DefinitelyTyped/issues/28884
    ref={ref}
    disabled={!enabled}
    onChange={ev => {
      const value = numeral(ev.target.value).value()
      onChange(value)
    }}
    onKeyPressCapture={ev => {
      if (ev.key === 'Enter') {
        ev.preventDefault()
        onConfirm()
      }
    }}
  />
))
